import React from "react";

export const MyIframe = () => {
  return (
    <div className="iframe">
      <MyIframe
        width="660"
        height="415"
        src="https://www.youtube.com/embed/04Kf_0kppPM"
        title="YouTube video"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen
      ></MyIframe>
    </div>
  );
};
